.checkbox__icon {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  left: 0;
  width: 25px;
  height: 25px;
  border-radius: 4px;
  border: 1px solid #fff;
}

.checkbox a {
  display: inline;
  border-bottom: 1px solid transparent;
  transition: border-color .3s;
}

.no-touch .checkbox a:hover {
  border-color: currentColor;
}

.checkbox input {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  border: 0;
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.checkbox input:checked ~ .checkbox__icon {
  background-image: url(../img/check.svg);
