.title {
	@include verdana;
	@include f(44px, 700, 1.23);
	color: $black;
	letter-spacing: normal;
	@include md {
		font-size: 25px;
		line-height: 1.33; } }
.title-m {
	@include verdana;
	@include f(30px, 700, 1.8);
	color: $black;
	letter-spacing: normal;
	@include sm {
		font-size: 25px;
		line-height: 1.24; } }
.title-lg {
	@include verdana;
	@include f(44px, 700, 1.22);
	color: $black;
	letter-spacing: normal;
	@include sm {
		font-size: 30px;
		line-height: 1.33; } }

.subtitle {
	@include verdana;
	@include f(18px, 700, 1.23);
	color: $text;
	letter-spacing: normal; }

